import React, {Component} from "react";
import "./MainSideClients.css";
import ClientsTable from "./ClientsTable/ClientsTable";
import AddClient from "./AddClient/AddClient";
import SideBar from "../../SideBar/SideBar";

class MainSideClients extends Component {
  state = {
    isNewClient: true,
    currentIndex: -1,
    list: this.returnList(),
  };

  AddClientsHandler = () => {
    this.setState((prevState) => ({
      isNewClient: !prevState.isNewClient,
      currentIndex: this.state.currentIndex,
      list: this.state.list,
    }));
  };

  returnList() {
    if (localStorage.getItem("leads") == null) {
      localStorage.setItem("leads", JSON.stringify([]));
    }
    return JSON.parse(localStorage.getItem("leads"));
  }

  handleEdit = (index) => {
    const Index = this.state.list.findIndex((s) => {
      return s.id === index;
    });

    const lead = {...this.state.list[Index]};
    lead.nameError = "";
    lead.emailError = "";
    lead.zipError = "";
    const leads = [...this.state.list];
    leads[Index] = lead;
    this.setState({
      currentIndex: index,
      list: leads,
    });
  };

  handleDelete = (index) => {
    var list = this.returnList();
    list.splice(index, 1);
    localStorage.setItem("leads", JSON.stringify(list));
    this.setState({currentIndex: -1, list});
  };

  onAddOrEdit = (data, id) => {
    var list = this.returnList();
    if (this.state.currentIndex === -1) {
      data.id = id;
      list.push(data);
    } else list[this.state.currentIndex] = data;
    localStorage.setItem("leads", JSON.stringify(list));
    this.setState({currentIndex: -1, list});
  };

  render() {
    return (
      <div className='container-fluid px-0'>
        <div className='d-flex'>
          <SideBar />
          <div className='px-0 w-100'>
            <div className='p-5 mt-2'>
              <span className='DashMarg'>Clients</span>
              <div className='col-12 pt-5 navBelowHead'></div>
            </div>
            {this.state.isNewClient ? (
              <button
                className='ml-5 px-4 py-3 addClients d-inline-block'
                onClick={this.AddClientsHandler}>
                <span>Add Clients</span>
              </button>
            ) : (
              <AddClient
                currentIndex={this.state.currentIndex}
                list={this.state.list}
                onAddOrEdit={this.onAddOrEdit}
                discard={this.AddClientsHandler}
              />
            )}
            <ClientsTable
              members={this.state.list}
              deleteItem={this.handleDelete}
              update={this.handleEdit}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default MainSideClients;
